import { Component } from '../base/Component';
import { CDN_URL } from '../../utils/constants';
export class ProductView extends Component {
    constructor(container, events) {
        super(container);
        this._hasPrice = true;
        this._events = events;
        this._title = this.container.querySelector('.card__title');
        this._description = this.container.querySelector('.card__text');
        this._price = this.container.querySelector('.card__price');
        this._image = this.container.querySelector('.card__image');
        this._category = this.container.querySelector('.card__category');
        this._button = this.container.querySelector('.card__button');
    }
    get isInTheBasket() {
        return this._isInTheBasket;
    }
    set isInTheBasket(value) {
        this._isInTheBasket = value;
    }
    set id(id) {
        this._id = id;
    }
    get id() {
        return this._id;
    }
    set category(category) {
        super.setText(this._category, category);
    }
    set title(title) {
        super.setText(this._title, title);
    }
    set description(description) {
        super.setText(this._description, description);
    }
    set price(price) {
        const text = price === null ? '' : price + ' ';
        super.setText(this._price, text);
    }
    set image(image) {
        super.setImage(this._image, CDN_URL + image);
    }
    delete() {
        this.container.remove();
        this.container = null;
    }
    stopBuy() {
        this.setDisabled(this._button, true);
    }
}
