import * as yup from 'yup';
export class ValidationService {
    constructor() {
        this.emailSchema = yup.string()
            .required(' ')
            .email('   ');
        this.phoneSchema = yup.string()
            .matches(/^(\+?\d{1,3}[- ]?)?\(?\d{3}\)?[- ]?\d{3}[- ]?\d{2}[- ]?\d{2}$/, '  ');
        this.nonEmptyStringSchema = yup.string()
            .required(' ')
            .test('non-empty', '    ', value => value.trim() !== '');
    }
    async checkEmail(value) {
        try {
            await this.emailSchema.validate(value);
            return true;
        }
        catch (error) {
            console.error('Email validation: ' + error);
            return false;
        }
    }
    async checkPhone(value) {
        try {
            await this.phoneSchema.validate(value);
            return true;
        }
        catch (error) {
            console.error('Phone validation: ' + error);
            return false;
        }
    }
    async checkNonEmptyString(value) {
        try {
            await this.nonEmptyStringSchema.validate(value);
            return true;
        }
        catch (error) {
            console.error('Not empty validation: ' + error);
            return false;
        }
    }
}
